import { Link } from "react-router-dom";
import React, { useEffect,useContext,useState} from "react";
import "./MyBooksDiv.css";
import { api_url } from "../..";
import AuthContext from "../../context/AuthContext";
import Book from "../book/Book";
import { useDrop } from "react-dnd";
function MyCurrentReads() {
  let { authTokens } = useContext(AuthContext);
  const [bookelements, Setbookelements] = useState("");

  const [{ isOver }, drop] = useDrop(() => ({
    accept: "book",
    drop: (item) => console.log(item),
    collect: (monitor) => ({
      isOver: !!monitor.isOver(),
    }),
  }));
useEffect(() => {
    fetch(`${api_url}api/getuserbooks/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + String(authTokens.access),
        },body:JSON.stringify({
          "book_read_status":"R"

        })
      }).then(resp => resp.json())
      .then(data => {var arr = [];
                  // eslint-disable-next-line no-unused-vars
                  for (const [key, value] of Object.entries(data.data)) {
                    arr.push(value)
                  }
                // for (var key in data.data) {
                //   if (data.data.hasOwnProperty(key)) {
                //     arr.push([data.data[key]]);
                //   }

                // }
                const listItems = arr.map((book) => (
                            // console.log(a)
                              <Book notes={book.notes} key={book.pk} book_read_status={book.book_read_status} online_read_url={book.online_read_url} read_completed_times={book.read_completed_times} cover_page_url={book.book_cover_url} bookId={book.pk}  name={book.name} about={book.about} author={book.author} total_pages={book.total_pages} pages_read={book.pages_read || 0}  />
                            ));
                            // console.log(arr)
                            Setbookelements(listItems)})
                // console.log(bookelements)            
   
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])



  
 
  return (
    <div  id="MyBooksDiv" ref={drop}>
      <p id="MyBooksDiv-head">Currently Reading</p>
      <div className="MyBooksDiv-body" id="My-current-reading-books-div">
        {bookelements}
        <div id="My-current-reading-books-text">
        Add your current reading books from My Library here.
        </div>
        </div>
      </div>
    
  );
}

export default MyCurrentReads;
